.page-navigator {
    flex: 0;
    order: 2;
    align-self: stretch;
    flex-grow: 0;
    margin: 10px 0px;
    margin-right: 150px;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
}
.page-marker {
    color: #FFFFFF;
    font-family: Fira Mono;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 46px;
    letter-spacing: 0px;
    text-align: left;
}